"use client";

import { useRouter, usePathname } from "next/navigation"; // ุงุฒ next/navigation
import { useTransition } from "react";

export default function LanguageSwitcher() {
  const router = useRouter();
  const pathname = usePathname();
  const [isPending, startTransition] = useTransition();

  const changeLocale = (locale: string) => {
    startTransition(() => {
      const segments = pathname.split("/");
      segments[1] = locale; // ูุฑุถ ุจุฑ ุงู ุงุณุช ฺฉู locale ุฏุฑ ุงุจุชุฏุง ูุณุฑ ุงุณุช
      const newPathname = segments.join("/");
      router.replace(newPathname);
    });
  };

  return (
    <div className="flex gap-2">
      <button onClick={() => changeLocale("fa")}>๐ฎ๐ท ูุงุฑุณ</button>
      <button onClick={() => changeLocale("en")}>๐บ๐ธ English</button>
      <button onClick={() => changeLocale("hy")}>๐ฆ๐ฒ ีีกีตีฅึีฅีถ</button>
    </div>
  );
}
